      background: #E7F4EF;
      border-radius: 16px 16px 0px 0px;
      font-size: 14px;
      font-family: Montserrat-Bold, Montserrat;
      font-weight: bold;
      color: #03A678;
    }
    &_box_progress {
      display: none;
    }
    &_box_progress_h5 {
      width: 340px;
      margin: 0 auto;
      padding: 38px 0 40px;
      border-bottom: 4px solid #E7F4EF;
      .countdown {
        font-size: 16px;
      }
    }
    &_box_progress_content {
      margin-top: 16px;
    }
    &_box_progress_content_title {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-bottom: 0;
        font-size: 14px;
        font-family: Montserrat-Regular, Montserrat;
        font-weight: 400;
        color: #959595;
        line-height: 18px;
        span {
          display: inline-block;
          margin-top: 8px;
          font-size: 18px;
          font-family: Montserrat-Bold, Montserrat;
          font-weight: bold;
          color: #4F4F4F;
          line-height: 20px;
        }
    }
    &_box_progress_content_progress {
        width: 280px;
        a {
          &:hover {
            color: #22292f;
            cursor: auto;
          }
        }
        .vote_progress {
          position: relative;
          margin-top: 16px;
          width: 280px;
          height: 10px;
          background: #E7F4EF;
          border-radius: 5px;
        }
        span {
          position: absolute;
          top: 0;
          left: 0;
          margin: 0;
          width: 0;
          height: 10px;
          background: #03A678;
          border-radius: 5px;
        }
    }
    &_box_progress_content_btn {
      display: flex;
      align-items: center;
      flex-direction: column;
      margin: 20px auto 0;
      a {
        display: inline-block;
        width: 100%;
        height: 40px;
        line-height: 34px;
        font-size: 18px;
        font-family: TestFoundersGrotesk-Semibold, TestFoundersGrotesk;
        font-weight: 600;
        color: #00C57B;
        text-align: center;
        background: #FFF;
        border-radius: 22px;
        border: 3px solid #00C57B;
        &:hover {
          box-shadow: 3px 3px 0px 0px #03A678;
        }
        &:last-child {
          margin-left: 0;
          margin-top: 10px;
          font-size: 16px;
          font-family: TestFoundersGrotesk-Semibold, TestFoundersGrotesk;
          font-weight: 600;
          color: #FFFFFF;
          background: #00C57B;
        }
      }
       .disable_failed {
        color: #D8D8D8 !important;
        border: 3px solid #D8D8D8;
        background: transparent !important;
        &:hover {
          box-shadow: none;
        }
      }
    }
    &_box_information {
       margin: 0 auto;
       width: 335px;
      .vote_box_information_title {
        margin-top: 30px;
        font-size: 28px;
        font-family: Montserrat-Bold, Montserrat;
        font-weight: bold;
        color: #4F4F4F;
        line-height: 34px;
      }
      p {
        margin-bottom: 0;
        font-size: 16px;
        font-family: Montserrat-Bold, Montserrat;
        font-weight: bold;
        color: #4F4F4F;
        line-height: 26px;
      }
      span {
        font-size: 14px;
        font-family: Montserrat-Regular, Montserrat;
        font-weight: 400;
        color: #4F4F4F;
        line-height: 26px;
      }
      .vote_box_information_about {
        margin-top: 20px;
      }
      .vote_box_information_url {
        margin-top: 10px;
      }
      .vote_box_information_info {
        margin-top: 18px;
        width: 340px;
        p {
          display: flex;
          flex-direction: column;
          margin-bottom: 28px;
        }
      }
    }
  }
}